@media only screen and (max-width: 500px) {
  .responsive__hidesidebox{
    animation-name: responsive__middlefade;
    animation-duration: 1s;
    animation-fill-mode: forwards;
  }
  .leftarrow{
    left: calc(45vw - 150px - 32px) !important;
  }
  .rightarrow{
    left: calc(45vw + 150px) !important;
  }
}

@media only screen and (max-width: 898px) and (min-width: 500px) {
  .responsive__hidesidebox{
    animation-name: responsive__middlefade;
    animation-duration: 1s;
    animation-fill-mode: forwards;
  }
  .leftarrow{
    left: calc(45vw - 150px - 35px - 32px) !important;
  }
  .rightarrow{
    left: calc(45vw + 150px + 35px) !important;
  }
}

#projectsmain{
  width: 300px;
  height:200px;
  border: ridge #3500d3;
  position: absolute;
  top:100px;
  left: calc(45vw - 150px);
  transition: height 1s, width 1s, top 1s, left 1s, opacity 1s;
  color: #40e0d0;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  text-align: center;
  z-index: 15;
}

#projectsmain:hover{
  background-color: #3500d3;
}

#projectsmain h3{
  margin: 20px 5px 30px 5px;
}

#projectsmain h4{
  margin: 0px 20px;
}

