.leftarrow{
  position: absolute;
  top: 185px;
  left: calc(45vw - 150px - 150px - 132px);
  color: #40e0d0;
  z-index: 50;
}

.rightarrow{
  position: absolute;
  top: 185px;
  left: calc(45vw + 150px + 150px + 100px);
  color: #40e0d0;
  z-index: 50;
}

#projectlayers i:hover{
cursor:pointer;
color: #35d300;

}

#projectlayers {
  max-width: 100% !important;
