
@media only screen and (max-width: 450px){
  .responsive__maintext h3{
    font-size: 1.05rem !important;
  }
}

@media only screen and (max-width: 600px) and (min-width: 450px){
  .responsive__maintext h3{
    font-size: 1.15rem !important;
  }
}

@media only screen and (max-width: 796px) and (min-width: 600px){
  .responsive__maintext h3{
    font-size: 1.25rem !important;
  }
}

@media only screen and (max-width: 900px) and (min-width: 796px){
  .responsive__maintext h3{
    font-size: 1.15rem !important;
  }
}

@media only screen and (max-width: 1150px) and (min-width: 900px){
  .responsive__maintext h3{
    font-size: 1.25rem !important;
  }
}

@media only screen and (min-width: 1150px){
  .responsive__maintext h3{
    font-size: 1.35rem !important;
  }
}

@media only screen and (max-width: 796px) {
  .responsive__heading{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 50px 0px;
  }
  .responsive__myprojects{
    position: absolute;
    top: 82rem;
  }
  .responsive__maintext{
    width: 60%;
  }
}

@media only screen and (min-width: 796px) {
  .responsive__heading{
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 50px 0px;
  }
  .responsive__myprojects{
    position: absolute;
    top: 75rem;
  }
  .responsive__maintext{
    width: 40%;
  }
}


#root {
  position: relative;
  max-width: 100vw !important;
  overflow-x: hidden !important;
}

#main {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: white;   
}

.my-section{
    color: #40e0d0;
    margin-top: 3rem;
}

.section0 {
  opacity: 0;
  animation-name: load;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

.section1{
    opacity: 0;
    animation-name: load;
    animation-delay: 3s;
    animation-duration: 1s;
    animation-fill-mode: forwards;
}

.horizontalline{
    width: 90vw;
    background-color: #0c0032;
    color: #35d300 !important;
}

.roadway {
    stroke-dasharray: calc(45vw + 25rem);
    animation: dash 10s linear infinite;
  }

.roadway__delayed {
    stroke-dasharray: calc(45vw + 25rem);
    animation: dash 5s linear infinite;
  }
  
  @keyframes dash {
    to {
      stroke-dashoffset: calc(90vw + 50rem);
    }
  }

  @keyframes load {
    to {
      opacity: 1;
    }
  }

  #button1{
    box-shadow:0px 0px 0px 1px #d38600 inset !important;
    color: #40e0d0 !important;
  }

#button2{
  box-shadow:0px 0px 0px 1px #3500d3 inset;
  color: #40e0d0;
  text-align: center;
  border-radius: 5px;
  padding: 9px 0;
}





